.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html {
  scroll-behavior: smooth;
}



.container-nav {
  width: 16%;
  height: 100vh;
border-right: 1px solid silver;
position: sticky;
top: 0;
left: 0;
}
.container-nav ul li Link {
  text-decoration: none;
}
.navlink {
  text-decoration: none;
  color: black;
}
.container-nav .logo img {
  width: 100px;
  margin-top: 16%;
  margin-left: 10%;


}

.nav-items ul {
  margin: 0;
  padding: 0px 20px;
}
.nav-items ul li {
  list-style: none;
  margin-top: 5%;
  padding: 10px;
  border-radius: 10px;
  
}
.nav-items ul li i {
 margin-right: 10%;
 font-size: 150%;

}
.nav-items ul :hover {
  background-color: #edecec;
}

.nav {
  height: 78%;
}
.buttom {
  padding: 0px 10px 0px 0px ;
}

.buttom i {
  font-size: 150%;
  padding: 10px 20px;
}
.feed {
 display: flex;
 flex-direction: row;
 margin-top: 4%;
 margin-left: 10%;
