}
.main {
  display: flex;
}
.container {
  width: 60px;
  height: 60px;
  border-radius: 100%;
  background-color: #964242;
  outline: 2px solid pink;
  border: 2px solid rgb(255, 255, 255);
  overflow: hidden;
  margin-top: 10px;
}
.root-story {
  margin-left: 3%;
  
}
.feed {
  overflow-x: scroll;
  height: 10%;
  width: 93%;

}

.feed ::-webkit-scrollbar-corner {
  display: none;
  /* -ms-overflow-style: none; */

}

.root-story h2 {
  font-size: 12px;
  text-align: center;
}
.container .img img {
  height: 70px;
}


