@media screen and (max-width:1265px) {
  .container-nav .navlink label, .container-nav label {
    display: none;
  }
  #instaicon {
    display: block;
    margin: 30px 0;
}
.container-nav {
  width: 8%;
}
}

@media screen and (max-width:1160px) {
  .leftroot {
    display: none;
  }
  .instafeed {
    width: 70%;
  }
}
@media screen and (max-width:765px) {
  .instafeed {
    width: 100%;
    z-index: -1;
  }
  .container {
    height: 90px;
    width: 90px;
    margin-top: 4vh;
  }
  #instaicon {
    display: none;
  }
  .container-nav {
    height: 10%;
    width: 100%;
    position: fixed;
    top: 91%;
    background-color: #fff;
  }
  .container-nav ul {
    display: flex;
    margin: auto;
    
  }
  .container-nav ul li {
    position: relative;
    font-size: 140%;
    margin:20px 20px
  }
